import { validateRegister } from "@/app/register/validateRegister";
import { User, LoginDetail } from "@/types/User";
import { LOGIN_URL, REGISTER_URL } from "@/utils/API_URL";
import axios from "axios";

export const userServices = {
    register: async (newUser: User) => {
        //validate newUser
        const newUserValid = validateRegister(newUser);
        if (!newUserValid) {
            alert("user is not valid to register");
            return;
        }
        //try create new user

        try {
            const res = await axios.post(REGISTER_URL, newUser);

            if (res.status === 200) {
                const { data } = res;
                console.log(data);
                return true;
            } else {
                alert("not ok");
                console.log("res is not ok");
                return false;
            }
        } catch (error) {
            console.log(error);
            return false;
        }
    },
    login: async (loginDetail: LoginDetail) => {
        try {
            const res = await axios.post(LOGIN_URL, loginDetail, {
                withCredentials: true,
            });
            console.log(res);
        } catch (error) {
            console.log(error);
            return false;
        }
    },
};
